.header {
    margin: 0;
}

.char {
    transition: all 500ms;
    transition: bottom 100ms;
    position: relative;
    bottom: 0;
    font-weight: bold;
    
    &.highlight {
        background: RGB(255,251,230);
        bottom: 2px;
    }
}


